import React, { useState } from 'react'
import '../styles/mainMenu.css'
import { Link } from 'gatsby'

export default function MainMenu() {
  const [menuState, setMenuState] = useState('hide');
  const toggleMenu = () => {
    if (menuState === 'hide') setMenuState('show')
    else setMenuState('hide');
  }
  return (
    <div>
      <div className={menuState === 'hide' ? '' : 'main-menu-modal'}>
        <div className={menuState === 'hide' ? "main-menu-links-mobile" : 'main-menu-links-mobile show-menu-mobile'}>
          <Link to='/'>HOME</Link>
          <Link to='/'>VÍDEOS</Link>
          <Link to='/noticias'>NOTÍCIAS</Link>
        </div>
      </div>
      <div className='main-menu'>
        <Link to='/'><img src="/pachecovit/images/pachecovit1.svg" alt="" /></Link>
        <div className="main-menu-links">
          <Link to='/'>HOME</Link>
          <Link to='/'>VÍDEOS</Link>
          <Link to='/noticias'>NOTÍCIAS</Link>
        </div>
        <button onClick={toggleMenu} className={menuState === 'hide' ? "toggle-button" : 'toggle-button toggle-button-fixed'}>
          <div className={menuState === 'hide' ? "bar1" : 'bar1 bar1-toggle'}></div>
          <div className={menuState === 'hide' ? "bar2" : 'bar2 bar2-toggle'}></div>
          <div className={menuState === 'hide' ? "bar3" : 'bar3 bar3-toggle'}></div>
        </button>
      </div>
    </div>
  )
}
